@use '@/styles/_colors.scss' as c;

.checkbox {
  display: flex;
  align-items: center;
  gap: 1em;

  &__label {
    display: flex;
    align-items: center;
    gap: 0.5em;
  }

  input[type='checkbox'] {
    accent-color: var(--color-ft, c.$ft-black);
    margin: 0;
    border: 0;
  }
}
